<template>
     <el-form :inline="true" :model="searchForm" ref="searchForm"  label-width="80px" >
       <el-row>
         <el-col :span="18">
            <el-form-item label="所属机构" prop="lineOrgUuid">
                <el-select v-model="searchForm.lineOrgUuid" placeholder="请选择">
                  <el-option
                    v-for="item in lineOrgs"
                    :key="item.orgUuid"
                    :label="item.orgName"
                    :value="item.orgUuid">
                  </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="线路名称" prop="lineName">
                <el-input v-model="searchForm.lineName" placeholder="请输入线路名称"></el-input>
            </el-form-item>
         </el-col>
         <el-col :span="6" class="flex-right">
            <el-form-item>
                <el-button type="primary" @click.native="search">查询</el-button>
                <el-button @click="resetForm('searchForm')">重置</el-button>
            </el-form-item>
         </el-col>
      </el-row>
      </el-form>
</template>

<script>
// api
import { getAllOrgList } from '@/api/common'
export default {
  name: 'LineManagementSearch',
  data () {
    return {
      lineOrgs: [], // 机构列表
      searchForm: {
        lineOrgUuid: '',
        lineName: ''
      }
    }
  },
  mounted () {
    getAllOrgList().then(res => {
      this.lineOrgs = res
    })
    this.search()
  },
  methods: {
    // 检索
    search: function () {
      this.$emit('search', Object.assign({}, this.searchForm))
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



