<template>
    <div>
      <el-dialog
      width="640px"
      title="新增线路信息"
      :visible.sync="isVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="cancel"
      >
        <el-form ref="lineForm" :model="lineForm" :rules="formRules" label-width="110px">

           <el-row>
              <el-col :span="12">
                 <el-form-item label="线路名称" prop="lineName">
                      <el-input v-model="lineForm.lineName" placeholder="请输入线路名称" />
                 </el-form-item>
              </el-col>
              <el-col :span="12">
                  <el-form-item label="所属机构" prop="lineOrgUuid">
                    <el-select v-model="lineForm.lineOrgUuid" @change="getLineList(lineForm.lineOrgUuid)">
                      <el-option
                        v-for="item in lineOrgs"
                        :key="item.orgUuid"
                        :label="item.orgName"
                        :value="item.orgUuid">
                      </el-option>
                    </el-select>
                  </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                 <el-form-item label="是否可用" prop="lineIsvalid">
                      <el-select v-model="lineForm.lineIsvalid">
                        <el-option label="启用" value="1"></el-option>
                        <el-option label="禁用" value="0"></el-option>
                      </el-select>
                  </el-form-item>
              </el-col>
              <el-col :span="12">
                 <el-form-item label="所属线路" prop="parLineUuid">
                    <el-select v-model="lineForm.parLineUuid">
                      <el-option
                        v-for="item in lines"
                        :key="item.lineUuid"
                        :label="item.lineName"
                        :value="item.lineUuid">
                      </el-option>
                    </el-select>
                  </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                  <el-form-item label="首班时间" prop="lineFirstTime">
                      <!-- <el-time-select
                        placeholder="起始时间"
                        v-entity="lineForm.lineFirstTime"
                        :picker-options="{
                          start: '05:00',
                          step: '00:15',
                          end: '23:00',
                          maxTime: lineForm.lineLastTime}"
                        >
                      </el-time-select> -->
                      <el-select v-model="lineForm.lineFirstTime" placeholder="起始时间" @change="firstChange">
                        <el-option
                        v-for="item in firstTimes"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                        :disabled="item.disabled">
                        </el-option>
                      </el-select>
                  </el-form-item>
              </el-col>
              <el-col :span="12">
                  <el-form-item label="末班时间" prop="lineLastTime">
                      <!-- <el-time-select
                        placeholder="结束时间"
                        v-entity="lineForm.lineLastTime"
                        :picker-options="{
                          start: '05:00',
                          step: '00:15',
                          end: '23:00',
                          minTime: lineForm.lineFirstTime
                        }">
                      </el-time-select> -->
                      <el-select v-model="lineForm.lineLastTime" placeholder="结束时间" @change="endChange">
                        <el-option
                        v-for="item in endTimes"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                        :disabled="item.disabled">
                        </el-option>
                      </el-select>
                  </el-form-item>
              </el-col>
            </el-row>
            <template>
                <el-row v-for="(line, index) in lineForm.lineDevices" :key="index">
                    <!-- {{line, index}} -->
                   <!-- <el-col :span="12" v-for="(item, i) in line" :key="index + '-' + i"> -->
                      <!-- <el-form-item :label="i === 'bldOriginalLineId' ? '线路编号' : '设备线路编号'"
                      :prop="'lineDevices.' + index + '.' + i"
                      :rules="{required: true, message: '数据不能空，且为数字值', trigger: 'blur', type: 'number'}">
                         <el-input v-entity.number="line[i]" :placeholder="i === 'bldOriginalLineId' ? '请输入线路编号' : '请输入设备线路编号'" />
                      </el-form-item> -->
                    <!-- </el-col> -->
                    <!-- <el-col :span="1" style="margin-left:10px;">
                      <el-button @click.prevent="deletRow(line,index)" :disabled="index === 0">删除</el-button>
                    </el-col> -->
                    <el-col :span="12" >
                      <el-form-item label="设备线路编号"
                      :prop="'lineDevices.' + index + '.' + 'bldDeviceLineId'"
                      :rules="[{required: true, message: '请输入设备线路编号', trigger: 'blur' }, {type: 'number',message: '设备线路编号必须为数字值'}]">
                         <el-input v-model.number="line.bldDeviceLineId" placeholder="请输入设备线路编号" @change="checkbldDeviceLine(line.bldDeviceLineId)"/>
                      </el-form-item>
                    </el-col>
                    <el-col :span="12" >
                      <el-form-item label="线路编号"
                      :prop="'lineDevices.' + index + '.' + 'bldOriginalLineId'"
                      :rules="{required: true, message: '请输入线路编号', trigger: 'blur'}">
                         <el-input v-model="line.bldOriginalLineId" placeholder="请输入线路编号" />
                      </el-form-item>
                    </el-col>
              </el-row>
            </template>
            <el-row>
                <el-col :span="6" :offset="18">
                    <!-- <el-button type="primary" @click="addRow">新增编号</el-button> -->
                    <el-button @click="cancel">取消</el-button>
                    <el-button type="primary" @click="save">保存</el-button>
                </el-col>
            </el-row>
       </el-form>
      </el-dialog>
    </div>
</template>
<script>
// api
import { getAllOrgList, linesList } from '@/api/common'
import { createLine } from '@/api/management'
export default {
  name: 'LineCreate',
  data () {
    return {
      firstTimes: [],
      endTimes: [],
      lineOrgs: [],
      lines: [],
      isVisible: false,
      lineForm: {
        lineName: '', // 线路名称
        parLineUuid: '', //  线路的父线路
        lineIsvalid: '', // 删除标记1：启用0：禁用
        lineOrgUuid: '', // 组织机构主键id
        lineFirstTime: '', // 首班时间
        lineLastTime: '', // 末班时间
        lineDevices: [{
          bldDeviceLineId: '', // 设备线路编
          bldOriginalLineId: '' // 线路编号
        }]
      },
      formRules: {
        lineName: [{required: true, message: '请输入线路名称', trigger: 'blur'}],
        lineOrgUuid: [{required: true, message: '请选择所属机构', trigger: 'change'}],
        lineIsvalid: [{required: true, message: '请选择是否可用', trigger: 'change'}]
      }

    }
  },
  props: {
    showCreate: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    showCreate (isShow) {
      this.isVisible = isShow
    }
  },
  mounted () {
    const startTime = '5:00'
    const endTime = '23:15'
    const stepTime = '15'
    let intTime = (parseInt(endTime.split(':')[0]) - (parseInt(startTime.split(':')[0]))) * (60 / parseInt(stepTime))
    let floatTime = parseInt(endTime.split(':')[1]) / parseInt(stepTime) - parseInt(startTime.split(':')[1]) / parseInt(stepTime)
    let allTime = intTime + floatTime
    for (let i = 0; i < allTime; i++) {
      var zeroTime = parseInt(startTime.split(':')[1]) // 15
      var tenTime = parseInt(startTime.split(':')[0]) // 5
      zeroTime += parseInt(stepTime) * i % 60
      tenTime += parseInt(parseInt(stepTime) * i / 60)
      if (zeroTime === 60) {
        zeroTime = 0
        tenTime += 1
      }
      var concatTime = tenTime < 10 ? '0' + tenTime : tenTime
      var concatzeroTime = zeroTime < 10 ? '0' + zeroTime : zeroTime
      this.firstTimes.push({value: concatTime + ':' + concatzeroTime, label: concatTime + ':' + concatzeroTime, disabled: false, index: i})
      this.endTimes.push({value: concatTime + ':' + concatzeroTime, label: concatTime + ':' + concatzeroTime, disabled: false, index: i})
    }

    getAllOrgList().then(res => {
      this.lineOrgs = res
    })
    const params = {
      lineOrgUuid: '',
      lineIsvalid: '1',
      lineDropFlag: 0
    }
    linesList(params).then(res => {
      this.lines = res
    })
  },
  methods: {
    firstChange (val) {
      for (let i = 0; i < this.endTimes.length; i++) {
        this.endTimes[i].disabled = false
        if (this.endTimes[i].value === val) {
          for (var j = 0; j <= i; j++) {
            this.endTimes[j].disabled = true
            if (this.endTimes[j].value === this.lineForm.lineLastTime) {
              this.lineForm.lineLastTime = ''
            }
          }
        }
      }
    },
    endChange (val) {
    //   console.log(val)
    },
    // 通过组织机构的id和启用，是否删除来获取当前线路
    getLineList (lineOrgUuid) {
      this.lineForm.parLineUuid = ''
      this.lines = []
      const params = {lineOrgUuid: lineOrgUuid, lineIsvalid: '1', lineDropFlag: 0}
      linesList(params).then(res => {
        this.lines = res
      })
    },
    save () {
      this.$refs.lineForm.validate(isValid => {
        if (isValid) {
          const params = Object.assign({}, this.lineForm)
          createLine(params).then(res => {
            if (!res.error) {
              this.$message({
                message: `新增路线成功!`,
                type: 'success'
              })
              this.cancel()
              this.$emit('createList')
            }
          })
        }
      })
    },
    cancel () {
      this.$refs.lineForm.resetFields()
      this.$emit('update:showCreate', false)
      this.isVisible = false
    },
    checkbldDeviceLine (val) {
      console.log(val)
    },
    addRow () {
      this.lineForm.lineDevices.push({bldDeviceLineId: '', bldOriginalLineId: ''})
    },
    deletRow (line, index) {
      this.lineForm.lineDevices.splice(index, 1)
    }
  }
}
</script>



