<template>
  <div>
    <!-- 面包屑 -->
    <!-- <header-breadcrumb :breadData="$route.matched" /> -->
    <nav-button>
        <el-button-group>
          <nav-premisson-button :comp="comp" :buttonData="buttonData"></nav-premisson-button>
        </el-button-group>
        <!-- <el-button-group>
          <el-button icon="fdddfont icon-add-new" @click="handleAdd">新增</el-button>
          <el-button icon="fdddfont icon-delete" :disabled="!selectRow.length" @click="batchRemove">删除</el-button>
          <el-button icon="fdddfont icon-shujudaoru" @click="exportsFile">导入</el-button>
        </el-button-group> -->
      </nav-button>
      <!-- 查询 -->
      <common-wrapper>
        <line-management-search class="search" @search="queryList" @rest="rest"/>
      </common-wrapper>
     <!-- 线路列表 -->
      <common-wrapper>
        <common-table :flex="230">
          <template slot="table">
            <el-table
              ref="roleTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="tableData"
              @selection-change="selected => selectRow = selected">
                <el-table-column type="selection" min-width="30"></el-table-column>
                <el-table-column type="index" :index="indexMethod" label="序号" min-width="50"></el-table-column>
                <el-table-column prop="lineName" label="线路名称" min-width="100"></el-table-column>
                <el-table-column  label="线路编号" min-width="100">
                  <template slot-scope="scope">{{scope.row.bldOriginalLineId || '' || null}}</template>
                </el-table-column>
                <el-table-column  label="设备线路编号" min-width="100">
                  <template slot-scope="scope">{{scope.row.bldDeviceLineId || '' || null}}</template>
                </el-table-column>
                <el-table-column prop="orgName" label="所属机构" min-width="120"></el-table-column>
                <el-table-column  label="所属线路" min-width="100">
                  <template slot-scope="scope">{{scope.row.parLineName || '' || null}}</template>
                </el-table-column>
                 <el-table-column  label="启禁状态" min-width="70">
                  <template slot-scope="scope">
                    <el-button  round @click="validLine(scope.$index, scope.row)">{{scope.row.lineIsvalid === '0' ? '禁用':'启用'}}</el-button>
                  </template>
                </el-table-column>
                <el-table-column prop="lineCreateTime" label="创建时间" :formatter="formatCreateDate" min-width="180"></el-table-column>
                <el-table-column prop="lineCreateUser" label="创建人" min-width="140">
                  <template slot-scope="scope">{{scope.row.lineCreateUser || '' || null}}</template>
                </el-table-column>
                <el-table-column label="操作" min-width="180">
                  <template slot-scope="scope">
                        <el-button size="mini" round icon="fdddfont icon-modify" @click.stop="handleEdit(scope.$index, scope.row)"></el-button>
                        <el-button size="mini" round icon="fdddfont icon-delete" @click.stop="handleDel(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>
      <!-- 线路新增 -->
      <line-create
        :showCreate.sync="isShowCreate"
        @createList="queryList"
      />
      <!-- 线路修改 -->
      <line-update
        :showEdit.sync="isShowEdit"
        :roleData.sync="editRole"
        @updateList="queryList"
      />

      <!-- 导入文件 -->
      <upload-file
         :showUpload.sync="showUpload"
         :uploadFileUrl="uploadFileUrl"
         :limit="limit"
         :size="size"
         :acceptType="acceptType"
         :pojo="pojo"
         :isShowDown="isShowDown"
         :isHasToken="isHasToken"
        @complete="getUploadData"/>
      <!-- 导入文件表格 -->
      <upload-file-table  :showUploadTable.sync="showUploadTable" :pojo="pojo" :uploadData.sync="uploadData" @updateList="queryList" v-if="uploadData.data"/>
  </div>
</template>
<script>
/**
* Line Management Module
* ----------------------
* Author: dengshijun
* Date: 2018.05.24
*/
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'

import LineManagementSearch from './Components/Search'
import LineUpdate from './Components/LineUpdate'
import LineCreate from './Components/LineCreate'
// 导入文件模块
import UploadFile from '../../Common/Upload'
import UploadFileTable from '../../Common/UploadTable'
import UploadLog from '../../Common/UploadLog'
// api
import { linepageSearch, deleteLine, isvalidLine } from '@/api/management'
export default {
  name: 'LineManagement',
  mixins: [mixins],
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: [], // 列表选中列
      editRole: {}, // 需要编辑的路线对象
      isShowCreate: false, // 新增窗口打开状态
      isShowEdit: false, //  编辑窗口打开状态
      isListLoading: false, // 列表加载
      // 导入需要传入的变量
      showUpload: false, // 导入窗口打开状态 ====>必填
      uploadFileUrl: '/excel/uploadFile', // 文件上传的url ====>必填
      limit: 1, // 上传文件的个数 默认为1
      size: 0.5, // 上传文件总的最大的容量（MB）默认为0.5
      acceptType: '.xls', // 上传文本格式  多个为'.xls,.png,.mp4' 默认为.xls
      pojo: 'TBusLine', // 上传的额外参数（线路信息导入）===> (非必填) 可以是字符串，数字，对象 在上传模块接受到的额外参数将以对象形式上传 {pojo: this.pojo}
      isShowDown: true, // 是否显示下载模板 ===> (非必填)
      isHasToken: true, // 是否需要上传token ===> (非必填)
      // 导入完成后的表格模块变量
      showUploadTable: false, // 导入列表打开状态
      uploadData: {}, // 批量导入返回的数据
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {} // 查询参数

    }
  },
  activated () {
    this.$refs.roleTable.doLayout()
  },
  watch: {

  },
  computed: {
  },
  mounted () {
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getRoles(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 获取列表
    getRoles (params) {
      this.isListLoading = true
      linepageSearch(params).then(res => {
        this.tableData = res.list
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getRoles(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 列表日期格式化
    formatCreateDate (row) {
      if (row.lineCreateTime) {
        return moment(row.lineCreateTime).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
      }
    },
    isButtonDisabled () {
      return !!this.selectRow.length
    },
    // 显示新增界面
    handleAdd () {
      this.isShowCreate = true
    },
    // 显示编辑界面
    handleEdit (index, row) {
      this.editRole = row
      this.isShowEdit = true
    },
    // 删除的接口方法
    deleteLines (lineIds) {
      return deleteLine(lineIds).then(res => {
        if (!res.error) {
          this.$message({
            message: '删除成功',
            type: 'success'
          })
          this.queryList()
        }
      })
    },
    // 批量删除
    batchRemove () {
      this.$confirm('确认删除选中的线路吗？', '提示', {
        type: 'warning'
      }).then(() => {
        let lineIds = []
        this.selectRow.map(row => lineIds.push(row.lineUuid))
        this.isListLoading = true
        this.deleteLines(lineIds).then(() => {
          this.isListLoading = false
        }).catch(() => {})
      })
    },
    // 单行删除
    handleDel (index, row) {
      this.$confirm('确认删除该线路吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.deleteLines([row.lineUuid])
      }).catch(() => {})
    },
    // 线路启用禁用
    validLine (index, row) {
      const validStatus = row.lineIsvalid === '0' ? '启用' : '禁用'
      this.$confirm(`确认状态修改为${validStatus}?`, '提示', {
        type: 'warning'
      }).then(() => {
        const params = {
          lineUuid: row.lineUuid,
          lineIsvalid: row.lineIsvalid
        }
        isvalidLine(params).then(res => {
          console.log(res)
          this.queryList()
        })
      }).catch(() => {})
    },
    // 批量导入
    exportsFile () {
      this.showUpload = true
    },
    // 批量导入数据
    getUploadData (params) {
      this.uploadData = params
      setTimeout(() => {
        this.showUploadTable = true
      }, 500)
    },
    // 导入查看log
    exportsLog () {
      this.$nextPage({
        title: `导入日志详情`,
        props: {
          modelName: '线路信息批量导入'
        },
        component: UploadLog, // 该组件会通过Webpack分开打包,并异步加载
        cache: true
      })
    }
  },
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    LineManagementSearch,
    LineUpdate,
    LineCreate,
    UploadFile,
    UploadFileTable
  }
}
</script>



